.AdminUserContainer {
  display: flex;
  margin: 0 auto 0 130px;
  height: 100vh;
  gap: 24px;

  .cardsContainer {
    width: 100%;
    height: 100vh;
    border-left: 1px solid #e6ecf0;
    border-right: 1px solid #e6ecf0;
    overflow-y: scroll;

    .cardList {
      margin: 16px;
      display: flex;
      flex-wrap: wrap;
      grid-gap: 16px;
    }
  }
}
