.adminMainContainer {
  display: flex;
  margin: 0 130px;
  gap: 24px;

  .subContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    border-left: 1px solid #e6ecf0;
    border-right: 1px solid #e6ecf0;
    overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 0px;
    }

    .listContainer {
      display: flex;
      flex-direction: column;
    }
  }
}
